@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
body{
  font-family: "Rubik";
}

.background{
  position: absolute;
  z-index: 0;
  width: 100vw;
}

.App {
  width: 100vw;
  height: 100vh;
  background-color: rgb(30, 27, 34);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
h1{  
  z-index: 1;
  color: white;
  display: flex;
justify-content: center;
align-items: center;
text-align: center;
font-size: 3rem;
}
.backIcon{
  width: 3rem;
  filter: brightness(0) invert(1);
  margin-right: 1rem;
  transition: .3s;
  cursor: pointer;
}
.backIcon:hover{
  width: 4rem;
  margin-right: 2rem;
}
.cardsSec{
  z-index: 1;
  width: 90%;
  /* background-color: rgb(101, 156, 178); */
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.selectedCard{
  justify-content: center;
}
.section{
  width: 22vw;
  height: 22vw;
  background-color: aqua;
  margin: 1rem;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: .4s;
  cursor: pointer;
  box-shadow: 0px 5px 40px -3px rgb(240 239 224);
  -webkit-box-shadow: 0px 5px 40px -3px rgb(240 239 224);
  -moz-box-shadow:0px 5px 40px -3px rgb(240 239 224);
}
.section img{
  width: 100%;
  height: 100%;
}
.section:hover{
  transition: .4s;
  width: 28vw;
  height: 28vw;
  
}

.true{
  width: 80vh;
  height: 80vh;
}
.true:hover{
  width: 80vh;
  height: 80vh;
}
@media (max-width: 1000px) {
  .true{
    width: 50vh;
    height: 50vh;
  }
  .true:hover{
    width: 50vh;
    height: 50vh;
  }
}
@media (max-width: 600px) {
  .true{
    width: 40vh;
    height: 40vh;
  }
  .true:hover{
    width: 40vh;
    height: 40vh;
  }
}
.false{
  width: 0%;

}
